	onLoad() {

	},
	methods: {
		clickshow: function() {
			this.showall = true;
			this.showview = false;
			this.showtravel = false;
			this.showflowerworld = false;
		},
		search: function() {
			this.showall = false;
			this.result = true;
		},
		clickshow1: function() {
			this.showall = false;
			this.showview = true;
			this.showtravel = false;
			this.showflowerworld = false;
		},
		clickshow2: function() {
			this.showall = false;
			this.showview = false;
			this.showtravel = true;
			this.showflowerworld = false;
		},
		clickshow3: function() {
			this.showall = false;
			this.showview = false;
			this.showtravel = false;
			this.showflowerworld = true;
		}
	},
	computed: {
		filterArticles: function() {
			var filterArray = this.msgs;
			var searchString = this.searchstr;
			//若搜索关键字为空则返回原始数据
			if (!searchString) {
				return filterArray;
			} else {
				//去除无用字符转换成小写
				searchString = searchString.trim().toLowerCase();
				//过滤数组内容
				filterArray = filterArray.filter(function(item) {
					//找到了标题含有searchString
					if (item.title.toLowerCase().indexOf(searchString) != -1) {
						return item;
					}
				})
				//最终的过滤数组
				return filterArray;
			}
		}
	}
}
